@mixin tablet {
    @media all and (min-width:768px) {
        @content
    }
}

@mixin desktop {
    @media all and (min-width:1200px) {
        @content
    }
}

@mixin flexcolumn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}